import { TaskPost } from "@/types/types";
import { Description } from "@headlessui/react";
import axios from "axios";
import 'dotenv/config'

const url = process.env.NEXT_PUBLIC_TODO_API

export const getToDo = async() =>{
    try{
        if(!url){
            throw new Error('no url provided')
        }
        console.log('trying to fetch')
        const tasks = await axios.get(url)
        console.log('task fetched', tasks)

        return tasks.data
    }catch(e){
        console.error(e)
    }
}

export const postToDo = async (data: TaskPost) =>{
    try{
        const fuckthis = process.env.NEXT_PUBLIC_TODO_API_1
        const res = await axios.post(fuckthis,{
            title: data.title,
            dueDate: new Date(data.dueDate).toISOString(),
            description: data.description,
            boardId: 1
        },
        {
            headers:{
            "Content-Type": "application/json"
            }
        }
    )
    }catch(e){
        console.error(e)
    }
}